import React, { useContext, useState, useEffect } from 'react'
import Leftnav from './Leftnav'
import { UserContext } from '../../store/UserContext';
export default function Adminhome() {
    var { getAllUser } = useContext(UserContext)
    var [user, setuser] = useState([])
    async function getApidata() {
        var response = await getAllUser()
        if (response.result === "Done") {
            setuser(response.data)
        }
    }
    useEffect(() => {
        getApidata();
    }, []);
    return (
        <>
            <div className="container-fluid">
                <div className="row mt-2">
                    <div className="col-xl-3  col-md-4 col-sm-6 col-12">
                        <Leftnav />
                    </div>
                    <div className="col-xl-9  col-md-8 col-sm-6 col-12">
                        <h3 className='background text-light text-center mb-1'>User List</h3>
                        <table className="table table-straped table-hover mt-2 p-2">
                            <tbody>
                                <tr>
                                    <th></th>
                                    <th>Id</th>
                                    <th>Name</th>
                                    <th>UserName</th>
                                    <th>Phone</th>
                                    <th>Gmail</th>
                                    <th>Role</th>
                                </tr>
                                {
                                    user.map((item,index)=>{
                                        return<tr key={index}>
                                        <td><img height="40px" widtd="40px" src={item.pic?`/public/images/${item.pic}`:""} alt=""/></td>
                                        <td>{item._id}</td>
                                        <td>{item.name}</td>
                                        <td>{item.username}</td>
                                        <td>{item.phone}</td>
                                        <td>{item.gmail}</td>
                                        <td>{item.role}</td>
                                    </tr>
                                    })
                                }
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>

        </>
    )
}
