import React, { useState, useContext, useEffect } from 'react'
import { useParams, Link } from 'react-router-dom';
import { BrandContext } from '../store/BrandContext'
import { MaincategoryContext } from '../store/MaincategoryContext'
import { SubcategoryContext } from '../store/SubcategoryContext'
import Product from './Product';
export default function Shop() {
    var { getAllMaincategory } = useContext(MaincategoryContext)
    var { getAllSubcategory } = useContext(SubcategoryContext)
    var { getAllBrand } = useContext(BrandContext)
    var [maincategory, setmaincategory] = useState([])
    var [subcategory, setsubcategory] = useState([])
    var [brand, setbrand] = useState([])
    var { mc, sc, br, search } = useParams()
    async function getApidata() {
        var response1 = await getAllMaincategory()
        setmaincategory(response1.data)
        var response2 = await getAllSubcategory()
        setsubcategory(response2.data)
        var response3 = await getAllBrand()
        setbrand(response3.data)
    }
    useEffect(() => {
        getApidata()
    }, [])
    return (
        <>
            <nav className="navbar navbar-expand-lg bg-light ">
                <div className="container-fluid background  ">

                    <ul className="nav">
                        <li className="nav-item dropdown">
                            <Link className="nav-link dropdown-toggle text-light fs-5 p-3 link-info" to="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                Maincategory
                            </Link>
                            <ul className="dropdown-menu">
                                <li><Link className="dropdown-item bg-info text-dark link-light" to={`/Shop/All/${sc}/${br}/${search}`}>All</Link></li>
                                {
                                    maincategory.map((item, index) => {
                                        return <li key={index}><Link className="dropdown-item bg-info text-dark link-light" to={`/Shop/${item.name}/${sc}/${br}/${search}`}>{item.name}</Link></li>

                                    })
                                }
                            </ul>
                        </li>
                        <li className="nav-item dropdown">
                            <Link className="nav-link dropdown-toggle text-light fs-5 p-3 link-info" to="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                Subcategory
                            </Link>
                            <ul className="dropdown-menu">
                                <li><Link className="dropdown-item bg-info text-dark link-light" to={`/Shop/${mc}/All/${br}/${search}`}>All</Link></li>
                                {
                                    subcategory.map((item, index) => {
                                        return <li key={index}><Link className="dropdown-item bg-info text-dark link-light" to={`/Shop/${mc}/${item.name}/${br}/${search}`}>{item.name}</Link></li>

                                    })
                                }
                            </ul>
                        </li>
                        <li className="nav-item dropdown">
                            <Link className="nav-link dropdown-toggle text-light fs-5 p-3 link-info" to="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                Brand          </Link>
                            <ul className="dropdown-menu">
                                <li><Link className="dropdown-item bg-info text-dark link-light" to={`/Shop/${mc}/${sc}/All/${search}`}>All</Link></li>
                                {
                                    brand.map((item, index) => {
                                        return <li key={index}><Link className="dropdown-item bg-info text-dark link-light" to={`/Shop/${mc}/${sc}/${item.name}/${search}`}>{item.name}</Link></li>

                                    })
                                }
                            </ul>

                        </li>


                    </ul>
                </div>
            </nav>
           
            <Product mc={mc} sc={sc} br={br} search={search} />
        </>
    )
}
