import React, { useState } from 'react'
import Grid from '@mui/material/Grid';
import { useNavigate,Link } from 'react-router-dom';

export default function Login() {
  let [login, setlogin] = useState(
    {
      username: '',
      password: ""
    }
  )
  const [isVisible, setVisible] = useState(false)
  const Show = () => {
    setVisible(!isVisible);
  };
  var navigate = useNavigate()
  function getdata(name, value) {
    setlogin((olddata) => {
      return {
        ...olddata,
        [name]: value
      }
    })
  }
  async function postdata() {
    var item = {
      username: login.username,
      password: login.password
    }
    var response = await fetch("/login", {
      method: "post",
      headers: {
        "content-type": "application/json"
      },
      body: JSON.stringify(item)
    })
    response = await response.json()
    if (response.result === "Done"){
      localStorage.setItem("login", true)
      localStorage.setItem("username", response.data.username)
      localStorage.setItem("name", response.data.name)
      localStorage.setItem("role", response.data.role)
      localStorage.setItem("userid", response.data._id)
      localStorage.setItem("token", response.token)
      navigate("/")
      
    }
    else
     alert (response.message)

  }
  return (
    <>
      <Grid container spacing={2}>
        <Grid item md={2} xs={12}>
        </Grid>
        <Grid item md={8} xs={12}>
          <h3 className='background mt-2 mb-1 text-light text-center p-2'>Login Section</h3>
          <div className="mb-3 ">
            <label >UserName</label>
            <input type="text" name='username' className="form-control" placeholder='Enter The UserName/Phone No' onChange={(e) => getdata(e.target.name, e.target.value)} />
          </div>
          <div className="mb-3">
            <label >Password</label>
            <input type={!isVisible ? "password" : "text"} name='password' className="form-control " id="inputPassword" onChange={(e) => getdata(e.target.name, e.target.value)} />

            <div className="row">
              <div className="col-10">
                <Link to='/forget-user' underline="none">Forget Password</Link>
              </div>
              <div className="col-2">
                <button className='btn text-primary' onClick={Show} >Show Password</button>
              </div>
            </div>
          </div>
          <button className='btn mybtn text-light text-center p-2 me-1 mb-3 ' onClick={postdata} >Login</button>
          <Link to="/signup" className='btn mybtn text-light text-center p-2 ms-1 mb-3 ' underline='none' >Create Account</Link>


        </Grid>
        <Grid item md={2} xs={12}>
        </Grid>
      </Grid>
    </>
  )
}
