import React,{useState,useEffect,useContext} from 'react'
import { MaincategoryContext } from '../../store/MaincategoryContext';
import Leftnav from './Leftnav'
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import AddIcon from '@mui/icons-material/Add';
import { Link } from 'react-router-dom';
export default function Adminmaincategory() {
    var{getAllMaincategory,deleteMaincategory}=useContext(MaincategoryContext)
    var [maincategory,setmaincategory]=useState([])
   async function deleterecord(_id){
        if(window.confirm("Are You Sure To Delete That That Item")){
            var item={
                _id:_id
            }
           var response = await deleteMaincategory(item)
              if (response.result === "Done")
            getApidata()
            else
            alert(response.message)
        
        }
    }
    async function getApidata(){
       var response = await getAllMaincategory()
          if (response.result === "Done")
        setmaincategory(response.data)
        else
        alert(response.message)
    }

    useEffect(()=>{
        getApidata()
    },[])
    return (
        <>
            <div className="container-fluid">
                <div className="row mt-2">
                    <div className="col-xl-3  col-md-4 col-sm-6 col-12">
                        <Leftnav />
                    </div>
                    <div className="col-xl-9  col-md-8 col-sm-6 col-12">
                        <h3 className='background text-light text-center mb-1'>Admin Maincategory<Link to="/admin-addmaincategory" className='btn link-primary text-light'><AddIcon/></Link></h3>
                        <table className='table table-striped table-hover'>
                            <tbody>
                                <tr>
                                    <th>id</th>
                                    <th>Name</th>
                                    <th></th>
                                    <th></th>
                                </tr>
                                { maincategory.map((item,index)=>{
                                    return <tr key={index}>
                                    <td>{item._id}</td>
                                    <td>{item.name}</td>
                                    <td><Link to={`/admin-updatemaincategory/${item._id}`} className='btn link-primary text-secondary'><EditIcon/></Link></td>
                                    <td><button className='btn link-primary text-secondary' onClick={()=>deleterecord(item._id)} ><DeleteIcon/></button></td>
                                </tr>
                                })
                                }
                               
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </>
    )
}
