import React from 'react'
import Carousel from 'react-material-ui-carousel'
import { Paper } from '@mui/material'
import pic1 from "../assets/image/banner1.jpg"
import pic2 from "../assets/image/banner2.jpg"
import pic3 from "../assets/image/banner3.jpg"
import pic4 from "../assets/image/banner4.jpg"
import pic5 from "../assets/image/banner5.jpg"
import pic6 from "../assets/image/banner6.jpg"
import pic7 from "../assets/image/banner7.jpg"
import pic8 from "../assets/image/banner8.jpg"
import pic9 from "../assets/image/banner9.jpg"
import pic10 from "../assets/image/banner10.jpg"
import Product from './Product'
var item=[
    {
        pic:pic1
    },
    {
        pic:pic2
    },
    {
        pic:pic3
    },
    {
        pic:pic4
    },
    {
        pic:pic5
    },
    {
        pic:pic6
    },
    {
        pic:pic7
    },
    {
        pic:pic8
    },
    {
        pic:pic9
    },
    {
        pic:pic10
    }
]
function Item(props){
    return(
        <Paper>
            <img src={props.item.pic} width='100%' height='500px' alt='' />
        </Paper>
    )
}
export default function Home() {
  return (
    <>
    <Carousel>
      {
        item.map((item,i)=><Item key={i} item={item} />)
        }
    </Carousel>
    <h5 className='background text-light text-center p-2 '> Product Section</h5>
    <Product mc="All" sc="All" br="All" search="None"/>
    </>
  )
}
