import React, { useState, useEffect,useContext } from 'react'
import Leftnav from './Leftnav'
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import AddIcon from '@mui/icons-material/Add';
import { Link } from 'react-router-dom';
import { SubcategoryContext } from '../../store/SubcategoryContext';
export default function Adminsubcategory() {
    var {getAllSubcategory,deleteSubcategory}=useContext(SubcategoryContext)
    var [subcategory, setsubcategory] = useState([])
    async function getdata(_id) {
        if (window.confirm("Are You Sure To Delete That Item")) {
            var item={
                _id:_id
            }
            var res = await deleteSubcategory(item)
            if (res.result === "Done")
                getApidata()
            else
                alert(res.message)
        }
    }
    async function getApidata() {
        var res = await getAllSubcategory()
        if (res.result === "Done")
            setsubcategory(res.data)
        else
            alert(res.message)
    }
    useEffect(() => {
        getApidata()
    }, [])
    return (
        <>
            <div className="container-fluid">
                <div className="row mt-2">
                    <div className="col-xl-3  col-md-4 col-sm-6 col-12">
                        <Leftnav />
                    </div>
                    <div className="col-xl-9  col-md-8 col-sm-6 col-12">
                        <h3 className='background text-light text-center mb-1'>Admin Subcategory<Link to="/admin-addsubcategory" className='btn link-primary text-light'><AddIcon /></Link></h3>
                        <table className='table table-striped table-hover'>
                            <tbody>
                                <tr>
                                    <th>id</th>
                                    <th>Name</th>
                                    <th></th>
                                    <th></th>
                                </tr>
                                {subcategory.map((item, index) => {
                                    return <tr key={index}>
                                        <td>{item._id}</td>
                                        <td>{item.name}</td>
                                        <td><Link to={`/admin-updatesubcategory/${item._id}`} className='btn link-primary text-secondary'><EditIcon /></Link></td>
                                        <td><button className='btn link-primary text-secondary' onClick={() => getdata(item._id)}><DeleteIcon /></button></td>
                                    </tr>
                                })
                                }

                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </>
    )
}
