import React, { useState } from 'react'
import Grid from '@mui/material/Grid';
import { Link } from '@mui/material';
import { useNavigate } from "react-router-dom"

export default function Signup() {
  var navigate = useNavigate()
  const [isVisible, setVisible] = useState(false)
  const Show = () => {
    setVisible(!isVisible);
  };
 
  let [register, setregister] = useState({
    name: "",
    username: "",
    phone: "",
    gmail: "",
    password: "",
    cpassword: ""
  })
  function getdata(name, value) {
    setregister((olddata) => {
      return {
        ...olddata,
        [name]: value
      }
    })
  }

  async function postdata() {
    if (register.password === register.cpassword) {
      var item = {
        name: register.name,
        username: register.username,
        phone: register.phone,
        gmail: register.gmail,
        password: register.password
      }
      var response = await fetch("/user", {
        method: "post",
        headers: {
          "content-type": "application/json"
        },
        body: JSON.stringify(item)
      })
      response = await response.json()
      if (response.result === "Done")
        navigate("/login")
      else
        alert(response.message)
    }
    else {
      alert("password not match")
    }
  }
  return (
    <>
      <Grid container spacing={2}>
        <Grid item md={2} xs={12}>
        </Grid>
        <Grid item md={8} xs={12}>
          <div className="mb-3">
            <label >Name</label>
            <input name="name" type="text" className="form-control" placeholder='Enter The Name' onChange={(e) => getdata(e.target.name, e.target.value)} />
          </div>
          <div className="mb-3">
            <label  >User Name</label>
            <input name="username" type="text" className="form-control" placeholder='Enter The User Name' onChange={(e) => getdata(e.target.name, e.target.value)} />
          </div>
          <div className="mb-3">
            <label  >Phone No</label>
            <input name="phone" type="number" className="form-control" placeholder='Enter The Phone No' onChange={(e) => getdata(e.target.name, e.target.value)} minLength={10} maxLength={12}  />
          </div>
          <div className="mb-3">
            <label  >Gmail</label>
            <input name="gmail" type="text" className="form-control" placeholder='Enter The Gmail' onChange={(e) => getdata(e.target.name, e.target.value)} />
          </div>
          <div className="mb-3">
            <label  >Password</label>
            <input name="password" type={!isVisible ? "password" : "text"} className="form-control" placeholder='Create passward' onChange={(e) => getdata(e.target.name, e.target.value)}  />
          </div>
          <div className="mb-3">
            <label  >Confirm Password</label>
            <input name="cpassword" type={!isVisible ? "password" : "text"} className="form-control" placeholder='Confirm passward' onChange={(e) => getdata(e.target.name, e.target.value)} minLength="8" />
            <button className='btn text-primary' onClick={Show}>Show Password</button>
          </div>
          <button className='btn mybtn  text-light text-center p-2 mt-1 w-100 ' onClick={postdata} >Sign Up</button>
          <div className=' text-center'>
            <Link underline='none' href='/Login' color="black">AlreadyUser SignIn?? </Link>
          </div>
        </Grid>
        <Grid item md={2} xs={12}>
        </Grid>
      </Grid>
    </>
  )
}
