import React from 'react'
import ReactDOM from 'react-dom/client'

import App from './component/app'
import Productcontextprovider from './store/Productcontext'
import CartContextProvider from './store/CartContext'
import WishlistContextProvider from './store/WishlistContext'
import CheckoutContextProvider from './store/CheckoutContext'
import MaincategoryContextProvider from './store/MaincategoryContext'
import SubcategoryContextProvider from './store/SubcategoryContext'
import BrandContextProvider from './store/BrandContext'
import UserContextProvider from './store/UserContext'
import NewslatterContextProvider from './store/NewslatterContext'
import ContactContextProvider from './store/ContactContext'
var root = ReactDOM.createRoot(document.getElementById("root"))
root.render(
    <>
        <Productcontextprovider>
            <CartContextProvider>
                <WishlistContextProvider>
                    <CheckoutContextProvider>
                        <MaincategoryContextProvider>
                          <SubcategoryContextProvider>
                         <BrandContextProvider>
                         <UserContextProvider>
                         <NewslatterContextProvider>
                        <ContactContextProvider>
                        <App/>
                        </ContactContextProvider>
                         </NewslatterContextProvider>
                         </UserContextProvider>
                         </BrandContextProvider>
                          </SubcategoryContextProvider>
                        </MaincategoryContextProvider>
                    </CheckoutContextProvider>
                </WishlistContextProvider>
            </CartContextProvider>
        </Productcontextprovider>
    </>
)