import React, { useContext, useState, useEffect } from 'react'
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import { CheckoutContext } from '../store/CheckoutContext';
import { CartContext } from '../store/CartContext';
import { UserContext } from '../store/UserContext';
import { useNavigate } from 'react-router-dom';

export default function Checkout() {
    var { AddCheckout } = useContext(CheckoutContext)
    var { getAllCart,deleteAllCart } = useContext(CartContext)
    var { getUser } = useContext(UserContext)
    var [shipping, setshipping] = useState([])
    var [total, settotal] = useState([])
    var [final, setfinal] = useState([])
    var [cart, setcart] = useState([])
    var [user, setuser] = useState({})
    var [mode, setmode] = useState("COD")
    var navigate = useNavigate()
    function changemode(e) {
        setmode(e.target.value)
    }
    async function AddToCheckout() {
        var item = {
            userid: localStorage.getItem("userid"),
            totalAmount: total,
            mode:mode,
            shippingAmount: shipping,
            finalAmount: final,
            city:`${user.city}`,
            date:`time:${new Date().getHours()}:${new Date().getMinutes()}|||date:${new Date().getDate()}/${new Date().getMonth()}/${new Date().getFullYear()}`,
            products: cart,
            rzpid:"0"
        }
        var response = await AddCheckout(item)
        if (response.result === "Done"){
            response=await deleteAllCart()
            if(mode==="COD")
             navigate("/confirm")
             else
             navigate("/payment/-1")
            }
           
        else{
            alert
                (response.message)
                navigate("/profile")
            }
    }

    async function getApiData() {
        var response = await getAllCart()
        if (response.result === "Fail")
            alert(response.message)

        setcart(response.data)
        var total = 0
        var shipping = 0
        for (let item of response.data) {
            total = total + item.total
        }
        if (total > 0 && total < 1000)
            shipping = 150
        settotal(total)
        setshipping(shipping)
        setfinal(total + shipping)

        response = await getUser()
        setuser(response.data)
    }
    useEffect(() => {
        getApiData()
    }, [])
    return (
        <div className="container-fluid">
            <div >
                    <div className='col-lg-4 col-sm-12'>
                        <h3 className='background text-light text-center p-2'>Billing Details</h3>
                        <table className="table table-straped table-hover mt-2 p-2">
                            <tbody>
                                <tr>
                                    <th>Name</th>
                                    <td>{user.name}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>UserName</th>
                                    <td>{user.username}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>Phone No</th>
                                    <td>{user.phone}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>Email</th>
                                    <td>{user.gmail}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>House No/ Flat No</th>
                                    <td>{user.addressline1}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>Villege/Nearby</th>
                                    <td>{user.addressline2}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>Sector</th>
                                    <td>{user.addressline3}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>PIN</th>
                                    <td>{user.pin}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>City</th>
                                    <td>{user.city}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>State</th>
                                    <td>{user.state}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className='col-lg-8 col-sm-12'>
                        <h3 className='background text-light text-center p-2'>Cart Details</h3>

                        <div className="container-fluid">

                            <div className="table-responsive tab">
                                <table className="table table-striped table-hover">
                                    <tbody>
                                        <tr>
                                            <th></th>
                                            <th>Name</th>
                                            <th>Color</th>
                                            <th>Size</th>
                                            <th>Price</th>
                                            <th>Qty</th>
                                            <th>Total</th>
                                        </tr>
                                    </tbody>
                                    <tbody>
                                        {
                                            cart.map((item, index) => {
                                                return <tr key={index}>
                                                    <td><img width="40px" height="45px" src={item.pic ? `/public/images/${item.pic}` : ""} alt="" /></td>
                                                    <td>{item.name}</td>
                                                    <td>{item.color}</td>
                                                    <td>{item.size}</td>
                                                    <td>&#8377;{item.price}</td>
                                                    <td>{item.qty}</td>
                                                    <td>&#8377;{item.total}</td>
                                                </tr>
                                            })
                                        }

                                    </tbody>
                                </table>
                           

                            <table className="table table-striped table-hover">
                                <tbody>
                                    <tr>
                                        <th>Total Amount</th>
                                        <td>&#8377;{total}</td>
                                    </tr>
                                    <tr>
                                        <th>Shipping Amount</th>
                                        <td>&#8377;{shipping}</td>
                                    </tr>
                                    <tr>
                                        <th>Final Amount</th>
                                        <td>&#8377;{final}</td>
                                    </tr>
                                    <tr>
                                        <th>Mode</th>
                                        <td>
                                            <select name="mode" className='form-select' onClick={changemode} >
                                                <option value="COD">COD</option>
                                                <option value="Net Banking">Net Banking/Card/UPI</option>
                                            </select>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                            <button className='btn bg-primary text-light w-100' onClick={AddToCheckout} >Place Order</button>
                        </div> </div>

                    </div>
            </div>
        </div>
    )
}
