import React, { useState, useContext } from 'react'
import { NewslatterContext } from '../store/NewslatterContext'


export default function Footer() {
  var [gmail, setgmail] = useState()
  var { AddNewslatter } = useContext(NewslatterContext)
  function getdata(e) {
    setgmail(e.target.value)
  }
  async function postdata(e) {
    e.preventDefault()
    var item = {
      date: `${new Date().getDate()}/${new Date().getMonth()}/${new Date().getFullYear()}`,
      gmail: gmail
    }
    var response = await AddNewslatter(item)
    alert(response.message)
  }

  return (
    <>
     <div className="mt-1">
     <div className="row background">
        <div className="col-md-2 col-sm-12">

        </div>
        <div className="col-md-8 col-sm-12 ">
          <h5 className='text-light text-center mt-2 '>CopyRight@FashionMart.com</h5>
          <form onSubmit={postdata}>
            <div className="form-group"  >
              <input type="email" name='gmail' className="form-control" placeholder="Drop Email Here !!! For Latest Product & Offers" onChange={getdata} />
              <button className="btn text-light bg-primary w-100 mb-2" type='submit'>Subscribe</button>
            </div>
          </form>
        </div>
        <div className="col-md-2 col-sm-12   ">

        </div>
      </div>
     </div>
    </>
  )
}
