import React, { useState,useContext,useEffect } from 'react'
import { useNavigate,useParams } from 'react-router-dom'
import { BrandContext } from '../../store/BrandContext'

import Leftnav from './Leftnav'

export default function Updatebrand() {
    var {updateBrand,getBrand}=useContext(BrandContext)
    var {_id}=useParams()
    var [name, setname] = useState("")
    var navigate = useNavigate()
    function getdata(e) {
        setname(e.target.value)
    }
   async function postdata(e){
    e.preventDefault()
    var item={
        name:name,
        _id:_id
    }
       var response=await updateBrand(item)
       if(response.result==="Done")
        navigate("/admin-brand")
        else
        alert(response.message)
    }
    async function getApidata(){
        var item={
            _id:_id
        }
        var response=await getBrand(item)
        if(response.result==="Done")
         setname(response.data)
         else
         alert(response.message)
    }
    useEffect(()=>{
        getApidata()
    },[])
    return (
        <>
            <div className="container-fluid">
                <div className="row mt-2">
                    <div className="col-xl-3  col-md-4 col-sm-6 col-12">
                        <Leftnav />
                    </div>
                    <div className="col-xl-9  col-md-8 col-sm-6 col-12">
                        <h3 className='background text-light text-center mb-1'>Update Brand</h3>
                        <form onSubmit={postdata}>
                            <input type="text" name='name' className='form-control p-3 mt-2' placeholder='Update Brand' onChange={getdata} value={name}/>
                            <button type='submit' className='btn bg-primary text-light link-seccess mt-1'>Update Brand</button>
                        </form>

                    </div>
                </div>
            </div>
        </>
    )
}
