import React, { useState } from 'react';
import "../assets/Style/style.css"
import { Link } from 'react-router-dom';
import pic from "../assets/image/user.jpg"
import { useNavigate } from 'react-router-dom';
import IconButton from '@mui/material/IconButton';
import ShoppingCartSharpIcon from '@mui/icons-material/ShoppingCartSharp';

export default function Navbar() {
  var [search,setsearch]=useState("None")
  var navigate = useNavigate()
  async function Logout() {
    var item = {
      username: localStorage.getItem("username"),
      token: localStorage.getItem("token")
    }
    var response = await fetch("/logout", {
      method: "post",
      headers: {
        "content-type": "application/json"
      },
      body: JSON.stringify(item)
    })
    response = await response.json()
    if (response.result === "Done") {
      localStorage.clear()
      navigate("/login")
    }
    else
      alert(response.message)
  }
  async function postData(e){
    e.preventDefault()
    navigate(`/shop/All/All/All/${search}`)
  }
 
  return (
    <>
      <div className="container-fluid" >
        <nav className="navbar navbar-expand-lg background sticky-top mb-2 ">
          <Link className="navbar-brand text-light fs-3 link-primary" to="/">Fashion Mart</Link>
          <button className="navbar-toggler bg-light me-1" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon link-light "></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link active text-light fs-5 link-primary" aria-current="page" to="/">Home</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link text-light fs-5 link-primary" to="/Shop/All/All/All/None">Shop</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link text-light fs-5 link-primary" to="/contact">Contact</Link>
              </li>
             {localStorage.getItem("role")==="Admin"?
                <li className="nav-item">
                  <Link className="nav-link text-light fs-5 link-primary" to="/admin-home">Admin</Link>
                </li>:""
             }
             
            </ul>
            <form className="d-flex " role="search" onSubmit={postData}>
              <input className="form-control me-2" type="search" name='search' placeholder="Search" aria-label="Search" onChange={(e)=>setsearch(e.target.value)} />
              <button className="btn btn-outline-light" type="submit">Search</button>
            </form>
            <ul className="navbar-nav  mb-1 mb-lg-0">

              {localStorage.getItem("login") ?
                <li className="dropdown-center w-100 me-3" >
                  <Link className="nav-link text-light" to="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    <img src={pic} width="45px" height="38px" style={{ borderRadius: "50%" }} alt=""></img>
                  </Link>
                  <ul className="dropdown-menu me-2 "  >
                    <li><Link className="dropdown-item " to="/profile">Profile </Link></li>
                    <li><Link className="dropdown-item " to="/cart">Cart<IconButton aria-label="cart">
                      <ShoppingCartSharpIcon color="primary" />
                    </IconButton></Link></li>
                    <li><Link className="dropdown-item " to="/order">Order </Link></li>
                    <li><button className="dropdown-item " onClick={Logout}>Logout</button></li>
                  </ul>
                </li>
                :
                <Link className='btn text-light' to="/login">Login</Link>
              }
            </ul>
          </div>
        </nav>
      </div>

    </>
  );
}

