import React, { useContext, useEffect, useState } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { CheckoutContext } from '../../store/CheckoutContext'
import Leftnav from './Leftnav'

export default function AdminSingleCheckout() {
    var { getCheckout, updateCheckout } = useContext(CheckoutContext)
    var [checkout, setcheckout] = useState([])
    var [status, setstatus] = useState()
    var [paymentstatus, setpaymentstatus] = useState()
    var { _id } = useParams()
    var navigate = useNavigate()
    function getData(e) {
        if (e.target.name === "status")
            setstatus(e.target.value)
        else
            setpaymentstatus(e.target.value)
    }
    async function Update() {
        var item = {
            _id: _id,
            paymentstatus: paymentstatus,
            status: status
        }
        var response = await updateCheckout(item)
        if (response.result === "Done")
            navigate("/admin-checkout")
        else
            alert(response.message)
    }
    async function getApidata() {
        var item = { _id: _id }
        var response = await getCheckout(item)
        if (response.result === "Done") {
            setcheckout(response.data)
            setstatus(response.data.status)
            setpaymentstatus(response.data.paymentstatus)
        }
        else
            alert(response.message)
    }
    useEffect(() => {
        getApidata()
    }, [])
    return (
        <>
            <div className='container-fluid'>
                <div className='row'>
                    <div className='col-xl-2 col-lg-3 col-md-4 col-sm-6 col-12'>
                        <Leftnav />
                    </div>
                    <div className='col-xl-10 col-lg-9 col-md-8 col-sm-6 col-12'>
                        <h5 className='background text-light text-center p-1'>Checkout List Page</h5>
                        <div className="table-responsive w-100">
                        <table className="table table-hover table-striped  p-1">
                            <tbody>
                                <tr>
                                    <th></th>
                                    <th>Name</th>
                                    <th>Color</th>
                                    <th>Size</th>
                                    <th>Brand</th>
                                </tr>
                                {
                                    checkout.products && checkout.products.map((item, index) => {
                                        return <tr key={index}>
                                            <td><img width="45px" height="45px" src={item.pic?`/public/images/${item.pic}`:""} alt="" /></td>
                                            <td>{item.name}</td>
                                            <td>{item.color}</td>
                                            <td>{item.size}</td>
                                            <td>{item.brand}</td>
                                        </tr>
                                    })
                                }
                            </tbody>
                        </table>
                        </div>
                        <table className="table table-hover  p-1">
                            <tbody>
                                <tr>
                                    <th>ID</th>
                                    <td>{checkout._id}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>Date</th>
                                    <td>{checkout.date}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>TotalAmount</th>
                                    <td>&#8377;{checkout.totalAmount}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>ShippingAmount</th>
                                    <td>&#8377;{checkout.shippingAmount}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>FinalAmount</th>
                                    <td>&#8377;{checkout.finalAmount}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>Mode</th>
                                    <td>{checkout.mode}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>RazerPayID</th>
                                    <td>{checkout.rzpid}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>Order Status</th>
                                    <td>{checkout.status}
                                        {checkout.status !== "Delivered" || checkout.paymentstatus !== "Done" ?
                                            <select name='status' onChange={getData} className="form-select">
                                                <option value="Not Packed">Not Packed</option>
                                                <option value="packed">packed</option>
                                                <option value="Ready to Ship">Ready to Ship</option>
                                                <option value="Shipped">Shipped</option>
                                                <option value="Out for Delivery">Out for Delivery</option>
                                                <option value="Delivered">Delivered</option>
                                            </select>
                                            : ""} </td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <th>Payment Status</th>
                                    <td>{checkout.paymentstatus}
                                        {checkout.status !== "Delivered" || checkout.paymentstatus !== "Done" ?
                                            <select name='paymentstatus' onChange={getData} className="form-select">
                                                <option value="Pending">Pending</option>
                                                <option value="Done">Done</option>
                                            </select> : ""}</td>
                                </tr>
                            </tbody>
                            <tbody>
                                <tr>
                                    <td><button className='btn w-100 mt-2 bg-primary' onClick={Update}>Update</button></td>
                                </tr>
                            </tbody>

                        </table>
                    
                        </div>
                    </div>
                </div>


        </>
    )
}
