import React,{useContext,useEffect,useState} from 'react'
import Leftnav from './Leftnav';
import { CheckoutContext } from '../../store/CheckoutContext';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import { Link } from 'react-router-dom';


export default function AdminCheckoutPage() {
   var {getAllAdminCheckout}=useContext(CheckoutContext)
   var [checkout,setcheckout]=useState([])
   async function getApidata(){
    var response=await getAllAdminCheckout()
    if(response.result==="Done")
    setcheckout(response.data)
    else
    alert(response.message)
   }
   useEffect(()=>{
    getApidata()
   },[])
    return ( 
        <div className='container-fluid'>
            <div className='row'>
                <div className='col-xl-2 col-lg-3 col-md-4 col-sm-6 col-12'>
                    <Leftnav/>
                </div>
                <div className='col-xl-10 col-lg-9 col-md-8 col-sm-6 col-12'>
                    <h5 className='background text-light text-center p-2'>Checkout List Page</h5>
              <div className="table-responsive">
              <table className='table '>
                        <tbody>
                            <tr>
                                <th>Date</th>
                                <th>Id</th>
                                <th>User Id</th>
                                <th>Mode</th>
                                <th>Status</th>
                                <th>PaymentStatus</th>
                                <th>Total</th>
                                <th></th>
                                
                            </tr>
                            
                          { checkout.map((item,index)=>{
                            return <tr key={index}>
                                <td>{item.date}</td>
                                <td>{item._id}</td>
                                <td>{item.userid}</td>
                                <td>{item.mode}</td>
                                <td>{item.status}</td>
                                <td>{item.paymentstatus}</td>
                                <td>&#8377;{item.totalAmount}</td>
                                <td><Link className='btn link-primary' to={`/admin-singlecheckout/${item._id}`}><RemoveRedEyeIcon/></Link></td>
                            </tr>
                            })
}
                        </tbody>
                    </table>
              </div>
                </div>
            </div>
        </div>
    )
}
