import React, { useContext, useState, useEffect } from 'react'
import Leftnav from './Leftnav'
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import AddIcon from '@mui/icons-material/Add';
import { Link } from 'react-router-dom';
import { Productcontext } from '../../store/Productcontext';
export default function Adminproduct() {
    var { getAllProduct, deleteProduct } = useContext(Productcontext)
    var [product, setproduct] = useState([])
    async function getdata(_id) {
        if (window.confirm("Are You Sure To Delete That Item")) {
            var item = {
                _id: _id
            }
            var res = await deleteProduct(item)
            if (res.result === "Done")
                getApidata()
            else
                alert(res.message)
        }
    }
    async function getApidata() {
        var res = await getAllProduct()
        if (res.result === "Done")
            setproduct(res.data)
        else
            alert(res.message)
    }
    useEffect(() => {
        getApidata()
    }, [])
    return (
        <>
            <div className="container-fluid">
                <div className="row mt-2">
                    <div className="col-xl-3  col-md-4 col-sm-6 col-12">
                        <Leftnav />
                    </div>
                    <div className="col-xl-9  col-md-8 col-sm-6 col-12" >
                        <h3 className='background text-light text-center mb-1'>Admin Product Page<Link to="/admin-addproduct" className='btn link-primary text-light'><AddIcon /></Link></h3>
                        <div className="table-responsive">
                            <table className='table table-striped table-hover'>
                                <tbody>
                                    <tr>
                                        <th>id</th>
                                        <th>Name</th>
                                        <th>Maincategory</th>
                                        <th>Subcategory</th>
                                        <th>Brand</th>
                                        <th>Color</th>
                                        <th>Size</th>
                                        <th>Baseprice</th>
                                        <th>Discount</th>
                                        <th>Finalprice</th>
                                        <th>Description</th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                    </tr>
                                    {product.map((item, index) => {
                                        return <tr key={index}>
                                            <td>{item._id}</td>
                                            <td>{item.name}</td>
                                            <td>{item.maincategory}</td>
                                            <td>{item.subcategory}</td>
                                            <td>{item.brand}</td>
                                            <td>{item.color}</td>
                                            <td>{item.size}</td>
                                            <td>&#8377;{item.baseprice}</td>
                                            <td>{item.discount}%</td>
                                            <td>&#8377;{item.finalprice}</td>
                                            <td>{item.description}</td>
                                            <td><img src={`./public/images/${item.pic1}`} width="40px" height="40px" alt="" /></td>
                                            <td><img src={`./public/images/${item.pic2}`} width="40px" height="40px" alt="" /></td>
                                            <td><img src={`./public/images/${item.pic3}`} width="40px" height="40px" alt="" /></td>
                                            <td><img src={`./public/images/${item.pic4}`} width="40px" height="40px" alt="" /></td>
                                            <td><Link to={`/admin-updateproduct/${item._id}`} className='btn text-secondary link-primary'><EditIcon /></Link></td>
                                            <td><button className='btn text-secondary link-primary' onClick={() => getdata(item._id)}><DeleteIcon /></button></td>
                                        </tr>
                                    })
                                    }

                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
