import React, { useContext, useState, useEffect } from 'react'
import Grid from '@mui/material/Grid';
import { Link } from 'react-router-dom';
import DeleteIcon from '@mui/icons-material/Delete';
import { WishlistContext } from "../store/WishlistContext"
import ShoppingCartSharpIcon from '@mui/icons-material/ShoppingCartSharp';
import { UserContext } from '../store/UserContext';
import pic from "../assets/image/no.png"

export default function Profile() {

    var { getWishlist, deleteWishlist } = useContext(WishlistContext)
    var { getUser } = useContext(UserContext)
    var [user, setuser] = useState({})
    var [wishlist, setwishlist] = useState([])

    async function DeleteWishlist(_id) {
        var item = {
            _id: _id
        }
        var response = await deleteWishlist(item)
        if (response.result === "Done")
            getApidata()
        else
            alert(response.message)
    }
    async function getApidata() {
        var response = await getWishlist()
        if (response.result === "Done") {
            setwishlist(response.data)
            response = await getUser()
            setuser(response.data)
        }

    }

    useEffect(() => {
        getApidata();
    }, []);
    
    return (
        <>
            <Grid container spacing={2}>
                <Grid item md={6} xs={12}>
                    <img src={user.pic ? `/public/images/${user.pic}` : pic} alt="" width="100%" height="505px" />
                </Grid>
                <Grid item md={6} xs={12}>
                    <table className="table table-straped table-hover mt-2 p-2">
                        <tbody>
                            <tr>
                                <th>Name</th>
                                <td>{user.name}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>UserName</th>
                                <td>{user.username}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>Phone</th>
                                <td>{user.phone}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>Gmail</th>
                                <td>{user.gmail}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>House No</th>
                                <td>{user.addressline1}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>Villege</th>
                                <td>{user.addressline2}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>Sector</th>
                                <td>{user.addressline1}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>PIN</th>
                                <td>{user.pin}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>City</th>
                                <td>{user.city}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>State</th>
                                <td>{user.state}</td>
                            </tr>
                        </tbody>
                    </table>
                    <Link className='btn mybtn text-light w-100 text-center p-2 ' to={`/updateprofile/${user._id}`}> Update Profile</Link>
                </Grid>
            </Grid>
            <div className="container-fluid">
                <h3 className='background text-light text-center p-2 mt-2'>Wishlist Section</h3>
                <div className="table-responsive">
                    <table className="table table-striped table-hover">
                        <tbody>
                            <tr>
                                <th></th>
                                <th>Name</th>
                                <th>Color</th>
                                <th>Size</th>
                                <th>Price</th>
                                <th></th>
                                <th></th>
                            </tr>
                        </tbody>
                        <tbody>
                            {
                                wishlist.map((item, index) => {
                                    return <tr key={index}>
                                        <td><img src={`/public/images/${item.pic}`} className="" style={{ width: "100px", height: "70px" }} alt="" /></td>
                                        <td>{item.name}</td>
                                        <td>{item.color}</td>
                                        <td>{item.size}</td>
                                        <td>&#8377;{item.price}</td>
                                        <td><Link to={`/single-product-page/${item.productid}`} className='btn'onClick={() => DeleteWishlist(item._id)} ><ShoppingCartSharpIcon /></Link></td>
                                        <td><button className='btn' onClick={() => DeleteWishlist(item._id)}><DeleteIcon /></button></td>

                                    </tr>
                                })
                            }
                        </tbody>
                    </table>
                </div>

            </div>


        </>
    )
}
