import React, { useState, useEffect, useContext } from 'react'
import Leftnav from './Leftnav';
import { NewslatterContext } from '../../store/NewslatterContext';
export default function AdminNewslatter() {
    var [newslatter, setnewslatter] = useState([])
    var { getAllNewslatter } = useContext(NewslatterContext)

    async function getApidata() {
        var response = await getAllNewslatter()
        if (response.result === "Done")
            setnewslatter(response.data)
        else
            alert(response.message)
    }
    useEffect(() => {
        getApidata()
    }, [])
    return (
        <div className='container-fluid'>
            <div className='row'>
                <div className='col-xl-2 col-lg-3 col-md-4 col-sm-6 col-12'>
                    <Leftnav />
                </div>
                <div className='col-xl-10 col-lg-9 col-md-8 col-sm-6 col-12'>
                    <h5 className='background text-light text-center p-2'>Newslatter List Page</h5>
                    <table className='table'>
                        <tbody>
                            <tr>
                                <th>Date</th>
                                <th>Id</th>
                                <th>Email</th>
                            </tr>
                           {
                            newslatter.map((item,index)=>{
                                return <tr key={index}>
                                    <td>{item.date}</td>
                                <td>{item._id}</td>
                                <td>{item.gmail}</td>
                            </tr>
                            })
                           }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}
