import React, { useState } from 'react'
import Grid from '@mui/material/Grid';
import { Link } from '@mui/material';
import { useNavigate } from 'react-router-dom';

export default function ForgetUser() {
    let [username, setusername] = useState(
    )

    var navigate = useNavigate()
    function getdata(value) {
        setusername(value)
    }
    async function postdata() {
        var item = {
            username: username,
        }
        var response = await fetch("/resetpassword-user", {
            method: "post",
            headers: {
                "content-type": "application/json"
            },
            body: JSON.stringify(item)
        })
        response = await response.json()
        if (response.result === "Done") {
            localStorage.setItem("username", username)
            alert(response.message)  
            navigate("/forget-otp")   
        }
        else
            alert(response.message)

    }
    return (
        <>
            <Grid container spacing={2}>
                <Grid item md={2} xs={12}>
                </Grid>
                <Grid item md={8} xs={12}>
                    <h3 className='background mt-2 mb-1 text-light text-center p-2'>Forget Password Section</h3>
                    <div className="mb-3 ">
                        <label >UserName</label>
                        <input type="text" name='username' className="form-control" placeholder='Enter The UserName ' onChange={(e) => getdata(e.target.value)} />
                    </div>
                    <button className='btn bg-primary w-100 text-light text-center p-2 me-1 mb-3 ' onClick={postdata} >Send OTP</button>
                </Grid>
                <Grid item md={2} xs={12}>
                </Grid>
            </Grid>
        </>
    )
}
