import React, { useState, useContext, useEffect } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { MaincategoryContext } from '../../store/MaincategoryContext'
import Leftnav from './Leftnav'

export default function Updatemaincategory() {
    var { updateMaincategory, getMaincategory } = useContext(MaincategoryContext)
    var [name, setname] = useState("")
    var { _id } = useParams()
    var navigate = useNavigate()
    function getdata(e) {
        setname(e.target.value)
    }
    async function postdata(e) {
        e.preventDefault()
        var item = {
            name: name,
            _id: _id
        }
        var response = await updateMaincategory(item)
        if (response.result === "Done") {
            navigate("/admin-maincategory")
        }
        else
            alert(response.message)
    }
    async function getApidata() {
        var item = {
            _id: _id
        }
        var response = await getMaincategory(item)
        if (response.result === "Done") {
            setname(response.data.name)
        }
        else
            alert(response.message)
    }

useEffect(() => {
    getApidata()
},[])
return(
    <>
        <div className="container-fluid">
            <div className="row mt-2">
                <div className="col-xl-3  col-md-4 col-sm-6 col-12">
                    <Leftnav />
                </div>
                <div className="col-xl-9  col-md-8 col-sm-6 col-12">
                    <h3 className='background text-light text-center mb-1'>Update Maincategory</h3>
                    <form onSubmit={postdata}>
                        <input type="text" name='name' className='form-control p-3 mt-2' placeholder='Update Maincategory' onChange={getdata} value={name} />
                        <button type='submit' className='btn bg-primary text-light link-seccess mt-1' onClick={postdata}>Update Maincategory</button>
                    </form>

                </div>
            </div>
        </div>
    </>
)
}
