import React, { useContext, useState, useEffect } from 'react'
import Carousel from 'react-material-ui-carousel'

import { useParams, useNavigate } from 'react-router-dom';
import { Productcontext } from '../store/Productcontext';
import { Paper } from '@mui/material';
import { CartContext } from "../store/CartContext"
import { WishlistContext } from "../store/WishlistContext"
export default function SingleProductPage() {
    var { getProduct } = useContext(Productcontext)
    var { AddCart, getAllCart } = useContext(CartContext)
    var { AddWishlist, getWishlist } = useContext(WishlistContext)
    var [product, setproduct] = useState({})
    var Navigate = useNavigate()
    var { _id } = useParams()
    var Items = [
        {
            pic: product.pic1
        },
        {
            pic: product.pic2
        },
        {
            pic: product.pic3
        },
        {
            pic: product.pic4
        }
    ]
    function Item(props) {
        return (
            <Paper>
                <img src={props.item.pic ? `/public/images/${props.item.pic}` : ""} width='95%' height='500px' alt='' />
            </Paper>
        )
    }
    async function AddToCart() {
        var response = await getAllCart()
        if (response.result === "Fail")
            Navigate("/login")
        if (response.data && response.data.find((item) => item.userid === localStorage.getItem("userid") && item.productid === _id) === undefined) {
            let item = {
                userid: localStorage.getItem("userid"),
                productid: product._id,
                name: product.name,
                maincategory: product.maincategory,
                subcategory: product.subcategory,
                brand: product.brand,
                color: product.color,
                size: product.size,
                price: product.finalprice,
                qty: 1,
                total: product.finalprice,
                date: new Date(),
                pic: product.pic1,
            }
            response = await AddCart(item)
            if (response.result === "Done")
                Navigate("/cart")
            else
                alert(response.message)
        }
        else
            Navigate("/cart")
    }
    async function AddToWishlist() {
        var response = await getWishlist()
        if (response.result === "Fail")
            Navigate("/login")
        if (response.data && response.data.find((item) => item.userid === localStorage.getItem("userid") && item.productid === _id) === undefined) {
            let item = {
                userid: localStorage.getItem("userid"),
                productid: product._id,
                name: product.name,
                maincategory: product.maincategory,
                subcategory: product.subcategory,
                brand: product.brand,
                color: product.color,
                size: product.size,
                price: product.finalprice,
                qty: 1,
                total: product.finalprice,
                pic: product.pic1,
            }
            response = await AddWishlist(item)
            if (response.result === "Done")
                Navigate("/profile")
            else
                alert(response.message)
        }
        else
            Navigate("/profile")
    }
    async function getApidata() {
        var item = {
            _id: _id
        }
        var response = await getProduct(item)
        if (response.result === "Done")
            setproduct(response.data)
        else
            alert(response.message)
    }
    useEffect(() => {
        getApidata()
    }, [])
    return (
        <>
            <div className="container-fluid">
                <div className='row'>
                    
                        <div className='col-lg-6 col-sm-12'>
                            <Carousel>
                                {
                                    Items.map((item, i) => <Item key={i} item={item} />)
                                }
                            </Carousel>
                        </div>

                        <div className='col-lg-6 col-sm-12'>
                            <table className="table table mt-2 p-3">
                                <tbody>
                                    <tr>
                                        <th>Name</th>
                                        <td>{product.name}</td>
                                    </tr>
                                </tbody>
                                <tbody>
                                    <tr>
                                        <th>Maincategory</th>
                                        <td>{product.maincategory}</td>
                                    </tr>
                                </tbody>
                                <tbody>
                                    <tr>
                                        <th>Subcategory</th>
                                        <td>{product.subcategory}</td>
                                    </tr>
                                </tbody>
                                <tbody>
                                    <tr>
                                        <th>Brand</th>
                                        <td>{product.brand}</td>
                                    </tr>
                                </tbody>
                                <tbody>
                                    <tr>
                                        <th>Size</th>
                                        <td>{product.size}</td>
                                    </tr>
                                </tbody>
                                <tbody>
                                    <tr>
                                        <th>Color</th>
                                        <td>{product.color}</td>
                                    </tr>
                                </tbody>
                                <tbody>
                                    <tr>
                                        <th>Base Price</th>
                                        <td>{product.baseprice}</td>
                                    </tr>
                                </tbody>
                                <tbody>
                                    <tr>
                                        <th>Final price</th>
                                        <td>{product.finalprice}</td>
                                    </tr>
                                </tbody>
                                <tbody>
                                    <tr>
                                        <th>Discount</th>
                                        <td>{product.discount}</td>
                                    </tr>
                                </tbody>
                                <tbody>
                                    <tr>
                                        <th>Stock</th>
                                        <td>{product.stock}</td>
                                    </tr>
                                </tbody>
                                <tbody>
                                    <tr>
                                        <th>Description</th>
                                        <td>{product.description}</td>
                                    </tr>
                                </tbody>
                            </table>
                            <button className='btn background mb-2 text-light p-2' style={{ width: "49%" }} onClick={AddToCart} >Add to Cart</button>
                            <button className='btn background mb-2 ms-1 text-light p-2' style={{ width: "49%" }} onClick={AddToWishlist}  >Add to Wishlist</button>

                        </div>
                   
                </div>
            </div>
        </>
    )
}
