import React from 'react'
import { BrowserRouter,Routes,Route } from 'react-router-dom'
import Navbar from "./navbar"
import Footer from './footer'
import Home from './home'
import Login from './login'
import Profile from './profile'
import Shop from './shop'
import Signup from './signup'
import SingleProductPage from './SingleProductPage'
import Cart from './cart'
import Updateprofile from './Updateprofile'
import Checkout from './Checkout'
import Contact from './contact'
import Adminhome from './Admin/Adminhome'
import Adminuser from './Admin/Adminuser'
import Adminmaincategory from './Admin/Adminmaincategory'
import Adminsubcategory from './Admin/Adminsubcategory'
import Adminbrand from './Admin/Adminbrand'
import Adminproduct from './Admin/Adminproduct'
import AdminNewslatter from './Admin/Adminnewslatter'
import AdminContactPage from './Admin/Admincontact'
import Addmaincategory from './Admin/Addmaincategory'
import Addsubcategory from './Admin/Addsubcategory'
import Addbrand from './Admin/Addbrand'
import Updatemaincategory from './Admin/Updatemaincategory'
import Updatesubcategory from './Admin/Updatesubcategory'
import Updatebrand from './Admin/Updatebrand'
import UpdateProduct from './Admin/Updateproduct'
import AdminCheckoutPage from './Admin/Admincheckout'
import AdminSingleContact from './Admin/AdminSinglecontact'
import Addproduct from './Admin/Addproduct'
import Order from './Order'
import ConFirm from './ConFirm'
import AdminSingleCheckout from './Admin/AdminSingleCheckout'
import ForgetOtp from './ForgetOtp'
import ForgetPassword from './ForgetPassword'
import ForgetUser from './Forgetuser'
import Payment from './Payment'

export default function app() {
  return (
   <>
   <BrowserRouter>
   <Navbar/>
   <Routes>
    <Route path='/' element={<Home/>}/>
    <Route path='/Shop/:mc/:sc/:br/:search' element={<Shop/>}/>
    <Route path='/login' element={<Login/>}/>
    <Route path='/signup' element={<Signup/>}/>
    <Route path='/profile' element={!localStorage.getItem("login")?<Login/>:<Profile/>}/>
    <Route path='/single-product-page/:_id' element={<SingleProductPage/>}/>
    <Route path='/cart' element={!localStorage.getItem("login")?<Login/>:<Cart/>}/>
    <Route path='/updateprofile/:_id' element={!localStorage.getItem("login")?<Login/>:<Updateprofile/>}/>
    <Route path="/checkout" element={!localStorage.getItem("login")?<Login/>:<Checkout/>}/>
    <Route path="/contact" element={<Contact/>}/>
    <Route path="/order" element={!localStorage.getItem("login")?<Login/>:<Order/>}/>
    <Route path="/confirm" element={!localStorage.getItem("login")?<Login/>:<ConFirm/>}/>
    <Route path="/forget-user" element={<ForgetUser/>}/>
    <Route path="/forget-otp" element={<ForgetOtp/>}/>  
    <Route path="/forget-password" element={<ForgetPassword/>}/>
    <Route path="/payment/:_id" element={!localStorage.getItem("login")?<Login/>:<Payment/>}/>


    <Route path="/admin-home" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Adminhome/>}/>
    <Route path="/admin-user" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Adminuser/>}/>
    <Route path="/admin-maincategory" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Adminmaincategory/>}/>
    <Route path="/admin-subcategory" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Adminsubcategory/>}/>
    <Route path="/admin-brand" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Adminbrand/>}/>
    <Route path="/admin-product" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Adminproduct/>}/>
    <Route path="/admin-newslatter" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<AdminNewslatter/>}/>
    <Route path="/admin-contact" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<AdminContactPage/>}/>
    <Route path="/admin-addmaincategory" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Addmaincategory/>}/>
    <Route path="/admin-addsubcategory" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Addsubcategory/>}/>
    <Route path="/admin-addbrand" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Addbrand/>}/>
    <Route path="/admin-updatemaincategory/:_id" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Updatemaincategory/>}/>
    <Route path="/admin-updatesubcategory/:_id" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Updatesubcategory/>}/>
    <Route path="/admin-updatebrand/:_id" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Updatebrand/>}/>
    <Route path="/admin-addproduct" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<Addproduct/>}/>
    <Route path="/admin-updateproduct/:_id" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<UpdateProduct/>}/>
    <Route path="/admin-checkout" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<AdminCheckoutPage/>}/>
    <Route path="/admin-singlecheckout/:_id" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<AdminSingleCheckout/>}/>
     <Route path="/admin-singlecontact/:_id" element={!localStorage.getItem("login")?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<AdminSingleContact/>}/>
   </Routes>
   </BrowserRouter>
   <Footer/>
   </>
  )
}
