import { CheckoutContext } from '../store/CheckoutContext';
import React, { useContext,useState,useEffect} from 'react'

export default function Order() {
    var [checkout,setcheckout]=useState([])
    var { getAllCheckout } = useContext(CheckoutContext)

 async function getApidata(){
           var response=await getAllCheckout()
            if(response.result==="Done")
            setcheckout(response.data)
            else
            alert(response.message)  
    }
   
    useEffect(()=>{
        getApidata()
    },[])
    
    return (
        <>
            <h3 className='background text-light text-center p-2 mt-2 mb-1'>Order History Section</h3>
            <div className="container-fluid">

                {
                    checkout.map((item, index) => {
                        return <div key={index} className="row">
                            <div className="col-lg-8 col-md-12" >
                                <div className="table-responsive">
                                <table className='table table-striped table-hover' >
                                    <tbody>
                                        <tr>
                                            <th>Date</th>
                                            <th>OrderID</th>
                                            <th>Payment Mode</th>
                                            <th>Order Status</th>
                                            <th>Payment Status</th>
                                            <th>Total</th>
                                            <th>Shipping</th>
                                            <th>Final Amount</th>
                                        </tr>
                                        <tr>
                                            <td>{item.date}</td>
                                            <td>{item._id}</td>
                                            <td>{item.mode}</td>
                                            <td>{item.status}</td>
                                            <td>{item.paymentstatus}</td>
                                            <td>&#8377;{item.totalAmount}</td>
                                            <td>&#8377;{item.shippingAmount}</td>
                                            <td>&#8377;{item.finalAmount}</td>
                                        </tr>
                                    </tbody>
                                </table>
                                </div>
                            </div>
                            <div className="col-lg-4 col-md-12">
                                <table className='table table-striped table-hover' >
                                    <tbody>
                                        <tr>
                                            <th></th>
                                            <th>name</th>
                                            <th>Size</th>
                                            <th>Color</th>
                                            <th>Subcategory</th>
                                        </tr>
                                        {
                                            item.products.map((p, index) => {
                                                return <tr key={index}>
                                                    <td><img src={p.pic?`/public/images/${p.pic}`:""} style={{ height: "50px", width: "50px" }} alt="" /></td>
                                                    <td>{p.name}</td>
                                                    <td>{p.size}</td>
                                                    <td>{p.color}</td>
                                                    <td>{p.subcategory}</td>
                                                </tr>
                                            })
                                        }
                                    </tbody>
                                </table>
                            </div>
                            <hr style={{ border: "5px solid gray" }} />
                        </div>
                    })
                }


            </div>
        </>
    )
}
