import React from 'react'
import { Link } from 'react-router-dom'
import pic from "../assets/success.jpg"
export default function ConFirm() {
  return (
    <>
    <img src={pic} alt="" width="95%" height="400px" />
    <h3 className='text-dark bg-success text-center mt-2'>Thanks To Shopping With Us</h3>
    <h4 className='text-dark bg-success text-center '>Check Your Shipping Details </h4>
    <Link  className='btn text-primary text-center link-dark w-100' to="/order">Click Here </Link>

    </>
  )
}
