import React, { useState } from 'react'
import Grid from '@mui/material/Grid';
import { useNavigate } from 'react-router-dom';

export default function ForgetPassword() {

    let [reset, setreset] = useState({
        password:"",
        cpassword:""
    })
    const [isVisible, setVisible] = useState(false)
    const Show = () => {
      setVisible(!isVisible);
    };

    var navigate = useNavigate()
    function getdata(name, value) {
        setreset((olddata) => {
            return {
                ...olddata,
                [name]: value
            }
        })
    }
    async function postdata() {
        if (reset.password===reset.cpassword) {
            var item = {
                username:localStorage.getItem("username"),
                password: reset.password
            }
            var response = await fetch("/resetpassword-password", {
                method: "post",
                headers: {
                    "content-type": "application/json"
                },
                body: JSON.stringify(item)
            })
            response = await response.json()
            if (response.result === "Done") {
                navigate("/login")
                alert(response.message)
            }
            else
                alert(response.message)

        }
        else
        alert("Invalid Password")
    }
    return (
        <>
            <Grid container spacing={2}>
                <Grid item md={2} xs={12}>
                </Grid>
                <Grid item md={8} xs={12}>
                    <h3 className='background mt-2 mb-1 text-light text-center p-2'>Forget Password Section</h3>
                    <div className="mb-3 ">
                        <label >Password</label>
                        <input type={!isVisible ? "password" : "text"} name='password' className="form-control" placeholder='Enter The Password ' onChange={(e) => getdata(e.target.name,e.target.value)} />
                    </div>
                    <div className="mb-3 ">
                        <label >Confirm Password</label>
                        <input type={!isVisible ? "password" : "text"} name='cpassword' className="form-control" placeholder='Enter The Password ' onChange={(e) => getdata(e.target.name,e.target.value)} />
                        <button className='btn text-primary' onClick={Show} >Show Password</button>
                    </div>
                    <button className='btn bg-primary w-100 text-light text-center p-2 me-1 mb-3 ' onClick={postdata} >Reset Password</button>
                </Grid>
                <Grid item md={2} xs={12}>
                </Grid>
            </Grid>
        </>
    )
}
