import React, { useContext, useEffect, useState } from 'react'
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';
import DeleteIcon from '@mui/icons-material/Delete';
import { CartContext } from '../store/CartContext'
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import { Link, useNavigate } from 'react-router-dom';

export default function Cart() {
    var { getCart, getAllCart, deleteCart, updateCart } = useContext(CartContext)
    var [cart, setcart] = useState([])
    var navigate = useNavigate()
    var [shipping, setshipping] = useState([])
    var [total, settotal] = useState([])
    var [final, setfinal] = useState([])
    async function deleteRecord(_id) {
        if (window.confirm("Are Your Sure to Delete that Item : ")) {
            var item = {
                _id: _id
            }
            var response = await deleteCart(item)
            if (response.result === "Done")
                getApiData()
            else
                alert(response.message)
        }
    }
    async function update(_id, op) {
        var response =await getCart({ _id: _id })
        var cart = response.data
        if (op === "DEC" && cart.qty === 1)
            return
        if (op === "DEC") {
            cart.qty = cart.qty - 1
            cart.total = cart.total - cart.price
        }
        else {
            cart.qty = cart.qty + 1
            cart.total = cart.total + cart.price
        }

        response = await updateCart(cart)
        if (response.result === "Done")
            getApiData()
        else
            alert(response.message)
    }
    async function getApiData() {
        var response = await getAllCart()
        if (response.result === "Fail")
            navigate("/login")


        setcart(response.data)
        var total = 0
        var shipping = 0
        for (let item of response.data) {
            total = total + item.total
        }
        if (total > 0 && total < 1000)
            shipping = 150
        settotal(total)
        setshipping(shipping)
        setfinal(total + shipping)
    }
    useEffect(() => {
        getApiData()
    }, [])
    return (
        <>
            {cart.length === 0 ? <h3 className='text-warning fs-1 text-center mt-4 p-3 '>No item in cart !!! Go Back to The Home Page <br /> <Link to="/">Click Here</Link> </h3> :
                <div className="container-fluid">

                    <div className="table-responsive">
                        <table className="table table-striped table-hover">
                            <tbody>
                                <tr>
                                    <th></th>
                                    <th>Name</th>
                                    <th>Color</th>
                                    <th>Size</th>
                                    <th>Price</th>
                                    <th></th>
                                    <th>Qty</th>
                                    <th></th>
                                    <th>Total</th>
                                    <th></th>
                                    
                                </tr>
                            </tbody>
                            <tbody>
                                {
                                    cart.map((item, index) => {
                                        return <tr key={index}>
                                            <td><img src={`/public/images/${item.pic}`} className="" style={{ width: "100px", height: "100px" }} alt="" /></td>
                                            <td>{item.name}</td>
                                            <td>{item.color}</td>
                                            <td>{item.size}</td>
                                            <td>&#8377;{item.price}</td>
                                            <td><button className='btn' onClick={() => update(item._id, "DEC")} ><RemoveIcon /></button></td>
                                            <td>{item.qty}</td>
                                            <td><button className='btn' onClick={() => update(item._id, "INC")}  ><AddIcon /></button></td>
                                            <td>&#8377;{item.qty * item.price}</td>
                                            <td><button className='btn' onClick={() => deleteRecord(item._id)}  ><DeleteIcon /></button></td>
                                        </tr>
                                    })
                                }
                            </tbody>
                        </table>
                    </div>
                    <Box sx={{ flexGrow: 1 }}>
                        <Grid container spacing={2}>
                            <Grid item md={6} xs={12}>
                            </Grid>
                            <Grid item md={6} xs={12}>
                                <table className="table table-striped table-hover">
                                    <tbody>
                                        <tr>
                                            <th>Total Amount</th>
                                            <td>&#8377;{total}</td>
                                        </tr>
                                        <tr>
                                            <th>Shipping Amount</th>
                                            <td>&#8377;{shipping}</td>
                                        </tr>
                                        <tr>
                                            <th>Final Amount</th>
                                            <td>&#8377;{final}</td>
                                        </tr>
                                    </tbody>
                                </table>
                                <Link to="/checkout" className='btn w-100 bg-primary text-light mb-1'>Checkout</Link>
                            </Grid>

                        </Grid>
                    </Box>
                </div>
            }
        </>
    )

}
