import React, { useState, useContext, useEffect } from 'react'
import Leftnav from './Leftnav'
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import AddIcon from '@mui/icons-material/Add';
import { Link } from 'react-router-dom';
import { BrandContext } from '../../store/BrandContext';
export default function Adminbrand() {
    var [brand, setbrand] = useState([])
    var { getAllBrand, deleteBrand } = useContext(BrandContext)
    
    async function getdata(_id) {
        var item = {
            _id: _id
        }
        var response = await deleteBrand(item)
        if (response.result === "Done")
            getApidata()
        else
            alert(response.message)
    }
    async function getApidata() {
        var response = await getAllBrand()
        if (response.result === "Done")
            setbrand(response.data)
        else
            alert(response.message)
    }
    useEffect(() => {
        getApidata()
    }, [])
    return (
        <>
            <div className="container-fluid">
                <div className="row mt-2">
                    <div className="col-xl-3  col-md-4 col-sm-6 col-12">
                        <Leftnav />
                    </div>
                    <div className="col-xl-9  col-md-8 col-sm-6 col-12">
                        <h3 className='background text-light text-center mb-1'>Admin Brand <Link to="/admin-addbrand" className='btn link-primary text-light'><AddIcon /></Link></h3>
                        <table className='table table-striped table-hover'>
                            <tbody>
                                <tr>
                                    <th>id</th>
                                    <th>Name</th>
                                    <th></th>
                                    <th></th>
                                </tr>
                                {brand.map((item, index) => {
                                    return <tr key={index}>
                                        <td>{item._id}</td>
                                        <td>{item.name}</td>
                                        <td><Link to={`/admin-updatebrand/${item._id}`} className='btn link-primary text-secondary'><EditIcon /></Link></td>

                                        <td><button className='btn link-primary text-secondary' onClick={()=>getdata(item._id)}><DeleteIcon /></button></td>
                                    </tr>

                                })
                                }
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </>
    )
}
