import React, { useContext, useState, useEffect } from 'react'
import Leftnav from './Leftnav'
import { UserContext } from '../../store/UserContext';
import pic from "../../assets/image/no.png"
export default function Adminhome() {
    var { getUser } = useContext(UserContext)
    var [user, setuser] = useState({})
    async function getApidata() {
        var  response = await getUser()
        if (response.result === "Done") {
            setuser(response.data)
        }

    }

    useEffect(() => {
        getApidata();
    }, []);
    return (
        <>
            <div className="container-fluid">
                <div className="row mt-2">
                    <div className="col-xl-3  col-md-4 col-sm-6 col-12">
                        <Leftnav />
                    </div>
                    <div className="col-xl-9  col-md-8 col-sm-6 col-12">
                        <div className="row">
                            <h3 className='background text-light text-center mb-1'>Admin Home Page</h3>
                            <div className="col-md-6 col-12">
                                <img src={user.pic?`/public/images/${user.pic}`:pic} width="100%" height="450px" alt='' className='rounded' />
                            </div>
                            <div className="col-md-6 col-12">
                            <table className="table table-straped table-hover mt-2 p-2">
                        <tbody>
                            <tr>
                                <th>Name</th>
                                <td>{user.name}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>UserName</th>
                                <td>{user.username}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>Phone</th>
                                <td>{user.phone}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>Gmail</th>
                                <td>{user.gmail}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>House No</th>
                                <td>{user.addressline1}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>Villege</th>
                                <td>{user.addressline2}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>Sector</th>
                                <td>{user.addressline1}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>PIN</th>
                                <td>{user.pin}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>City</th>
                                <td>{user.city}</td>
                            </tr>
                        </tbody>
                        <tbody>
                            <tr>
                                <th>State</th>
                                <td>{user.state}</td>
                            </tr>
                        </tbody>
                    </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
