import React, { useState,useContext } from 'react'
import { ContactContext } from '../store/ContactContext'
import {useNavigate} from "react-router-dom"
export default function Contact() {
    var {AddContact}=useContext(ContactContext)
    var navigate= useNavigate()
    var [state, setstate] = useState(
        {
            name: "",
            gmail: "",
            phone: "",
            subject: "",
            massege: ""
        }
    )
    function getdata(name, value) {
        setstate((olddata) => {
            return {
                ...olddata,
                [name]: value
            }
        })

    }
   async function postdata(e) {
    e.preventDefault()
        var item = {
            name: state.name,
            gmail: state.gmail,
            phone: state.phone,
            subject: state.subject,
            message: state.message,
            date: `${new Date().getDate()}/${new Date().getMonth()}/${new Date().getFullYear()}`,
            status:"Active"
        }
        var response = await AddContact(item)
        alert(response.message)
        navigate("/")
    }
    return (
        <>
            <div className="container-fluid">
                <div className="row mb-2">
                    <div className="col-md-6 col-12 ">
                        <div className="  text-dark text-center p-2 mt-2 ">
                            <h5>Gmail ID - FashionMart@gmail.com</h5>
                            <h5>Phone No - 120103003</h5>
                            <h5>Facebook Page- VTSFashionPage.Facebook.com </h5>
                        </div>
                        <div className="mapouter">
                            <div className="gmap_canvas" >
                                <iframe className="gmap_iframe ms-2" style={{ width: "95%", height: "400px" }} frameBorder="0" scrolling="no" marginHeight="0" marginWidth="0" src="https://maps.google.com/maps?width=600&amp;height=400&amp;hl=en&amp;q=gaur city mall &amp;t=&amp;z=14&amp;ie=UTF8&amp;iwloc=B&amp;output=embed">
                                </iframe>

                            </div>
                        </div>
                    </div>
                    <div className="col-md-6 col-12">
                        <h3 className='background text-light text-center p-2 mt-2'>Contact Us</h3>
                        <form onSubmit={postdata}>
                            <div className="form-group">
                                <label >Name</label>
                                <input type="text" name='name' className="form-control" placeholder="Enter Your Name" onChange={(e) => { getdata(e.target.name, e.target.value) }} />
                            </div>
                            <div className="form-group">
                                <label >Email address</label>
                                <input type="email" name='gmail' className="form-control" placeholder="Enter Your Email" onChange={(e) => { getdata(e.target.name, e.target.value) }} />
                            </div>
                            <div className="form-group">
                                <label >Phone No</label>
                                <input type="number" name='phone' className="form-control" placeholder="Enter Your Phone No" onChange={(e) => { getdata(e.target.name, e.target.value) }} />
                            </div>
                            <div className="form-group">
                                <label >Subject</label>
                                <input type="text" name='subject' className="form-control" placeholder="Enter Your Locality" onChange={(e) => { getdata(e.target.name, e.target.value) }} />
                            </div>

                            <div className="form-group">
                                <label >Message</label>
                                <textarea className="form-control" name='message' rows="4" onChange={(e) => { getdata(e.target.name, e.target.value) }}></textarea>
                            </div>
                            <button type='submit' className='btn bg-primary text-light mb-2'>Submit</button>
                        </form>
                    </div>
                </div>
            </div>
        </>
    )
}
