import React, { useContext, useState, useEffect } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import Leftnav from './Leftnav'
import { ContactContext } from '../../store/ContactContext';

export default function AdminSingleContact() {
    var navigate = useNavigate()
    var { _id } = useParams()
    var { getContact, updateContact } = useContext(ContactContext)
    var [contact, setcontact] = useState([])
    var [status, setstatus] = useState()
    function getdata(e) {
        setstatus(e.target.value)
    }
    async function updateRecord(_id) {
        var item = {
            _id: _id,
            status: status
        }
        var response = await updateContact(item)
        if (response.result === "Done")
            navigate("/admin-contact")
        else
            alert(response.message)
    }

    async function getApidata() {
        var item = {
            _id: _id
        }
        var response = await getContact(item)
        if (response.result === "Done") {
            setcontact(response.data)
        }
        else
            alert(response.message)
    }
    useEffect(() => {
        getApidata()
    }, [])
    return (
        <div className="container-fluid">
            <div className="row mt-2">
                <div className="col-xl-3  col-md-4 col-sm-6 col-12">
                    <Leftnav />
                </div>
                <div className="col-xl-9  col-md-8 col-sm-6 col-12" >
                    <h3 className='background text-light text-center mb-1'>Single Contact Page</h3>
                    <div className="table">
                        <table className='table table-striped table-hover'>
                            <tbody>
                                <tr>
                                    <th>Id</th>
                                    <td>{contact._id}</td>
                                </tr>
                                <tr>
                                    <th>Name</th>
                                    <td>{contact.name}</td>
                                </tr>
                                <tr>
                                    <th>Email</th>
                                    <td>{contact.gmail}</td>
                                </tr>
                                <tr>
                                    <th>Phone No</th>
                                    <td>{contact.phone}</td>
                                </tr>
                                <tr>
                                    <th>Date</th>
                                    <td>{contact.date}</td>
                                </tr>
                                <tr>
                                    <th>Subject</th>
                                    <td>{contact.subject}</td>
                                </tr>
                                <tr>
                                    <th>Message</th>
                                    <td>{contact.message}</td>
                                </tr>
                                <tr>
                                    <th>Status</th>
                                    <td> {contact.status !== "Done" ?
                                        <select name="status" onChange={getdata}>
                                            <option value="Active">Active</option>
                                            <option value="Done">Done</option>
                                        </select>
                                        : `${contact.status}`}
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                        {contact.status !== "Done" ? <button className='btn bg-primary text-light w-100' onClick={() => updateRecord(_id)}>Update</button>:""}
                    </div>
                </div>
            </div>
        </div>
    )
}
