import React, { useState } from 'react'
import Grid from '@mui/material/Grid';
import { useNavigate } from 'react-router-dom';

export default function ForgetOtp() {
    let [otp, setotp] = useState("0")

    var navigate = useNavigate()
    function getdata(value) {
        setotp(value)
    }
    async function postdata() {
        var item = {
            username:localStorage.getItem("username"),
            otp: otp
        }
        var response = await fetch("/resetpassword-otp", {
            method: "post",
            headers: {
                "content-type": "application/json"
            },
            body: JSON.stringify(item)
        })
        response = await response.json()
        if (response.result === "Done") 
            navigate("/forget-password")
        else
            alert(response.message)

    }
    return (
        <>
            <Grid container spacing={2}>
                <Grid item md={2} xs={12}>
                </Grid>
                <Grid item md={8} xs={12}>
                    <h3 className='background mt-2 mb-1 text-light text-center p-2'>Forget Password Section</h3>
                    <div className="mb-3 ">
                        <label >OTP</label>
                        <input type="number" name='otp' className="form-control" placeholder='Enter The OTP ' onChange={(e) => getdata(e.target.value)} />
                    </div>
                    <button className='btn bg-primary w-100 text-light text-center p-2 me-1 mb-3 ' onClick={postdata} >Verify</button>
                </Grid>
                <Grid item md={2} xs={12}>
                </Grid>
            </Grid>
        </>
    )
}
