import { Grid } from '@mui/material'
import React, { useState, useContext, useEffect } from 'react'
import { useNavigate, useParams } from "react-router-dom"
import { UserContext } from '../store/UserContext'
export default function Updateprofile() {
    var navigate = useNavigate()
    var { _id } = useParams()
    var { getUser, updateUser } = useContext(UserContext)
    let [user, setuser] = useState({
        name: "",
        phone: "",
        addressline1: "",
        addressline2: "",
        addressline3: "",
        pin: "",
        city: "",
        state: "",
        pic: ""
    })
    function getdata(name, value) {
        setuser((olddata) => {
            return {
                ...olddata,
                [name]: value
            }
        })
    }
    function getfile(name, files) {
        setuser((olddata) => {
            return {
                ...olddata,
                [name]: files
            }
        })
    }

    async function postdata(e) {
        e.preventDefault()
        var formData = new FormData()
        formData.append("name", user.name)
        formData.append("phone", user.phone)
        formData.append("addressline1", user.addressline1)
        formData.append("addressline2", user.addressline2)
        formData.append("addressline3", user.addressline3)
        formData.append("city", user.city)
        formData.append("state", user.state)
        formData.append("pin", user.pin)
        formData.append("pic", user.pic)
        var response = await updateUser(formData, _id)
        if (response.result === "Done")
            navigate("/profile")
        else
            alert(response.message)
    }
    async function getApidata() {
        var response = await getUser({_id:_id})
        if (response.result === "Done")
            setuser(response.data)
        else
            alert(response.message)
    }
    useEffect(() => {
        getApidata()
    }, [])
    return (
        <>
            <h3 className='background text-light p-2 mt-2 text-center'>Update Proflie Section</h3>
            <Grid container spacing={2}>
                <Grid item md={2} xs={12}>

                </Grid>
                <Grid item md={8} xs={12}>

                    <div className="mb-1  mt-2">
                        <form onSubmit={postdata}>
                            <label >Name </label>
                            <input name="name" type="text" className="form-control" placeholder='Enter The Name' onChange={(e) => getdata(e.target.name, e.target.value)} value={user.name} />
                            <div className="mb-1 ">
                                <label  >Phone No</label>
                                <input name="phone" type="number" className="form-control" placeholder='Enter The Phone No' onChange={(e) => getdata(e.target.name, e.target.value)} value={user.phone} />
                            </div>
                            <div className="mb-1 ">
                                <label >House NO</label>
                                <input name="addressline1" type="text" className="form-control" placeholder='Update Your House No' onChange={(e) => getdata(e.target.name, e.target.value)} value={user.addressline1} />
                            </div>
                            <div className="mb-1 ">
                                <label >Villege</label>
                                <input name="addressline2" type="text" className="form-control" placeholder='Update Your Village' onChange={(e) => getdata(e.target.name, e.target.value)} value={user.addressline2} />
                            </div>
                            <div className="mb-1 ">
                                <label >Sector</label>
                                <input name="addressline3" type="text" className="form-control" placeholder='Update Your Sector' onChange={(e) => getdata(e.target.name, e.target.value)} value={user.addressline3} />
                            </div>
                            <div className="mb-1 ">
                                <label >Pin</label>
                                <input name="pin" type="text" className="form-control" placeholder='Update Your PIN' onChange={(e) => getdata(e.target.name, e.target.value)} value={user.pin} />
                            </div>
                            <div className="mb-1 ">
                                <label >City</label>
                                <input name="city" type="text" className="form-control" placeholder='Update Your City' onChange={(e) => getdata(e.target.name, e.target.value)} value={user.city} />
                            </div>
                            <div className="mb-1 ">
                                <label >State</label>
                                <input name="state" type="text" className="form-control" placeholder='Upadte Your State' onChange={(e) => getdata(e.target.name, e.target.value)} value={user.state} />
                            </div>
                            <div className="mb-1 ">
                                <label  >Profile Pic</label>
                                <input name="pic" type="file" className="form-control" onChange={(e) => getfile(e.target.name, e.target.files[0])} />
                            </div>


                            <button type='submit' className='btn mybtn  text-light text-center p-2 mt-1 w-100 ' >Update</button>
                        </form>
                    </div>
                </Grid>
                <Grid item md={2} xs={12}>

                </Grid>
            </Grid>
        </>
    )
}
