import React from 'react'

import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { Link } from 'react-router-dom';

export default function Productitem(props) {
  return (
   <>
    <div className='container-fluid mt-1 w-100 '>
   <Link to={`/single-product-page/${props._id}`} style={{ textDecoration: 'none' }} >
  
    
        <Card sx={{ maxWidth:"95%",height:"45%" }}>
      <CardMedia
        component="img"
        alt="image"
        height="180"
        image={`/public/images/${props.pic}`}
      />
      <CardContent>
        <Typography gutterBottom variant="h8" component="div">
        {props.brand} {props.name}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          Price &#8377;<del>{props.baseprice}</del>  {props.finalprice}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          Discount {props.discount}%
        </Typography>
        <Typography variant="body2" color="text.secondary">
         {props.description}
        </Typography>
      </CardContent>
      <CardActions>
        <button className='bg-dark text-light mb-1 w-100'>Buy Now</button>
      </CardActions>
    </Card>
         </Link>
       </div>
    
   </>
  )
}
