import React, { useState,useContext } from 'react'
import { useNavigate } from 'react-router-dom'
import { MaincategoryContext } from '../../store/MaincategoryContext'
import Leftnav from './Leftnav'

export default function Addmaincategory() {
    var {AddMaincategory}=useContext(MaincategoryContext)
    var [name, setname] = useState("")
    var navigate = useNavigate()
    function getdata(e) {
        setname(e.target.value)
    }
   async function postdata(e){
    e.preventDefault()
        var item ={
            name:name
        }
      var response=await AddMaincategory(item)
          if (response.result === "Done")
        navigate("/admin-maincategory")
        else
        alert(response.message)
    }
    return (
        <>
            <div className="container-fluid">
                <div className="row mt-2">
                    <div className="col-xl-3  col-md-4 col-sm-6 col-12">
                        <Leftnav />
                    </div>
                    <div className="col-xl-9  col-md-8 col-sm-6 col-12">
                        <h3 className='background text-light text-center mb-1'>Add Maincategory</h3>
                        <form onSubmit={postdata}>
                            <input type="text" name='name' className='form-control p-3 mt-2' placeholder='Add Maincategory' onChange={getdata} />
                            <button type='submit' className='btn bg-primary text-light link-seccess mt-1'>Add Maincategory</button>
                        </form>

                    </div>
                </div>
            </div>
        </>
    )
}
